.Answers {
  display: flex;
  border-radius: 5px;
  overflow: hidden;
  height: 100%;
}

.Answers ul {
  list-style: none;
  width: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
  display: grid;
}

.Answers li {
  text-align: center;
  padding: 10px;
}

.Answers li:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.Answers li:hover {
  background-color: rgba(255, 115, 115, 0.3);
}

.border_top {
  border-top: 1px solid white;
}

.true {
  color: red;
}

.false {
  color: rgb(255, 115, 115);
}

.normal {
  color: white;
}