.WinWindow {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  justify-content: center;
  align-items: center;
  background-color: rgba(95, 77, 77, 0.8);
  z-index: 1;
}

.ModalWindow {
  padding: 10px;
  opacity: 0;
  width: 350px;
  height: 200px;
  max-width: 100%;
  max-height: 100%;
  border-radius: 5px;
  border: 1px solid white;
  background-color: rgb(184, 54, 54);
  transition: 1s;
  animation: show 3s 1;
  animation-fill-mode: forwards;
  animation-delay: 1s;
  z-index: 0;
  
}

.ModalWindow_Wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
  text-align: center;
}

.ModalWindow_Wrapper p:first-child {
  font-size: larger;
}

@keyframes show {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}